import React, { Component } from "react";
import TodoItem from "./TodoItem";
import Input from "./Input/Input";
import "./Todo.css";

export default class Todo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      todoItems: ["ss", "s", "sa", "as"],
      showInput: false
    };
    this.deleteItem = this.deleteItem.bind(this);
    this.addItem = this.addItem.bind(this);
    this.toggleInput = this.toggleInput.bind(this);
  }

  deleteItem(num) {
    const newArr = this.state.todoItems.filter((n) => n !== num);
    this.setState({ todoItems: newArr });
  }

  addItem(item) {
    const newArr = [...this.state.todoItems, item];
    this.setState({ todoItems: newArr });
  }

  toggleInput() {
    const showInput = !this.state.showInput;
    this.setState({ showInput });
  }

  render() {
    const { showInput, todoItems } = this.state;
    return (
      <div id="todo">
        <div id="header">
          <h1 style={{ textAlign: "center" }}> John's Todo List </h1>
          <button onClick={this.toggleInput} id={showInput ? "Close" : "Done"}>
            {showInput ? "CLOSE" : "INPUT"}
          </button>
        </div>
        <hr />
        {showInput && <Input addItem={this.addItem} />}
        <ol>
          {todoItems.map((num, index) => (
            <TodoItem key={index} number={num} deleteItem={this.deleteItem} />
          ))}
        </ol>
      </div>
    );
  }
}
