import "./Style.css";
import { MdDeleteForever } from "react-icons/md";

const TodoItem = ({ number, deleteItem }) => {
  return (
    <li className="item">
      Todo Item {number}
      <button onClick={() => deleteItem(number)}>
        <MdDeleteForever className="delete" color="red" size="30" />
      </button>
    </li>
  );
};

export default TodoItem;
